'use client';

import { useState, useEffect, useRef } from 'react';
import {
  ArrowLeft,
  Send,
  MessageCircle,
  User,
  Bot,
} from 'lucide-react';

import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';

type Message = {
  id: number;
  sender: 'user' | 'other';
  text: string;
};

type Chat = {
  id: number;
  name: string;
  messages: Message[];
};

export default function ChatPage() {
  const [selectedChatId, setSelectedChatId] = useState<number | null>(null);
  const [messageInput, setMessageInput] = useState('');
  const [chats, setChats] = useState<Chat[]>([
    {
      id: 1,
      name: 'John Doe',
      messages: [
        { id: 1, sender: 'other', text: 'Hello there!' },
        { id: 2, sender: 'user', text: 'Hey John!' },
      ],
    },
    {
      id: 2,
      name: 'Jane Smith',
      messages: [
        { id: 1, sender: 'other', text: 'Are you coming to the meeting?' },
        { id: 2, sender: 'user', text: 'Yes, I’ll be there.' },
      ],
    },
  ]);

  const messagesEndRef = useRef<HTMLDivElement>(null);

  const selectedChat = chats.find((chat) => chat.id === selectedChatId);

  const handleSend = () => {
    if (!messageInput.trim() || selectedChatId === null) return;

    setChats((prevChats) =>
      prevChats.map((chat) =>
        chat.id === selectedChatId
          ? {
              ...chat,
              messages: [
                ...chat.messages,
                {
                  id: chat.messages.length + 1,
                  sender: 'user',
                  text: messageInput.trim(),
                },
              ],
            }
          : chat
      )
    );

    setMessageInput('');
  };

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [selectedChat?.messages]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-black via-zinc-900 to-zinc-800 text-white flex flex-col">
      {/* Header */}
      <div className="bg-white/5 backdrop-blur-md border-b border-white/10 p-4">
        <div className="max-w-6xl mx-auto flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <MessageCircle className="h-6 w-6 text-white" />
            <h1 className="text-xl font-bold">Chat Dashboard</h1>
          </div>
        </div>
      </div>

      {/* Layout */}
      <div className="flex-1 flex overflow-hidden">
        {/* Sidebar */}
        <div className={`${selectedChatId === null ? 'block' : 'hidden'} lg:block w-full lg:w-80 bg-white/5 border-r border-white/10`}>
          <div className="p-4 border-b border-white/10 flex justify-between items-center">
            <h2 className="text-white font-semibold">Chats</h2>
            <span className="text-sm text-gray-300">{chats.length}</span>
          </div>
          <ScrollArea className="h-full">
            {chats.map((chat) => (
              <Card
                key={chat.id}
                onClick={() => setSelectedChatId(chat.id)}
                className="bg-white/5 hover:bg-white/10 border-white/10 cursor-pointer m-2"
              >
                <CardContent className="p-3">
                  <div className="text-white font-medium">{chat.name}</div>
                  <div className="text-gray-400 text-sm truncate">
                    {chat.messages.at(-1)?.text}
                  </div>
                </CardContent>
              </Card>
            ))}
          </ScrollArea>
        </div>

        {/* Chat Area */}
        <div className="flex-1 flex flex-col">
          {selectedChat ? (
            <>
              {/* Mobile back button */}
              <div className="lg:hidden bg-white/10 p-3 flex items-center space-x-2 border-b border-white/10">
                <Button variant="ghost" size="icon" onClick={() => setSelectedChatId(null)}>
                  <ArrowLeft className="text-white" />
                </Button>
                <h3 className="text-white font-semibold">{selectedChat.name}</h3>
              </div>

              {/* Messages */}
              <ScrollArea className="flex-1 p-4 space-y-3">
                {selectedChat.messages.map((msg) => (
                  <div
                    key={msg.id}
                    className={`flex ${msg.sender === 'user' ? 'justify-end' : 'justify-start'}`}
                  >
                    <div
                      className={`max-w-[80%] p-3 rounded-lg text-sm flex items-start space-x-2 ${
                        msg.sender === 'user'
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-700 text-white'
                      }`}
                    >
                      {msg.sender === 'user' ? (
                        <Bot className="h-4 w-4 mt-1" />
                      ) : (
                        <User className="h-4 w-4 mt-1" />
                      )}
                      <span className="break-words">{msg.text}</span>
                    </div>
                  </div>
                ))}
                <div ref={messagesEndRef} />
              </ScrollArea>

              {/* Input Bar */}
              <div className="p-4 border-t border-white/10 bg-white/5">
                <div className="flex gap-2">
                  <Input
                    value={messageInput}
                    onChange={(e) => setMessageInput(e.target.value)}
                    onKeyDown={(e) => e.key === 'Enter' && handleSend()}
                    className="flex-1 bg-white/10 border-white/20 text-white"
                    placeholder="Type your message..."
                  />
                  <Button
                    onClick={handleSend}
                    disabled={!messageInput.trim()}
                    className="bg-blue-600 text-white hover:bg-blue-700"
                  >
                    <Send className="h-4 w-4" />
                  </Button>
                </div>
              </div>
            </>
          ) : (
            <div className="flex-1 flex items-center justify-center text-gray-400">
              <div className="text-center">
                <MessageCircle className="h-12 w-12 mx-auto mb-4 opacity-50" />
                <h3 className="text-lg font-semibold">Select a chat to start</h3>
                <p className="text-sm">Choose a conversation from the sidebar</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
